<template>
    <div>
    <input ref="input" type="file" style="display:none">
    <img ref="img" class=" img-upload" alt="" :src="getPathValue(value)"></img>
</div>

</template>
<script type="text/javascript">
  import zwIcon from "../icon/zwIcon.vue"
  export default {
    name: 'zwInput',
    components: {
      zwIcon
    },
    props: {value:{type:String},
    img:{type:String}},
    data() {
      return {
        ok: false,
        textvalue: "",
        closeSee: false,
        imgDom: null, //imageid
        		maxHeight: null, //
        		maxWidth: null, //
        		postUrl: null, //url"/calendar/image/upload.json"
        		preShow: false,
        		callback: null,
                fileInput:null
      };
    },
    computed: {

    },
    mounted() {

        this.init({});
    },
    methods: {
    getPathValue:function(value){
        return getPathValue(value);
    },
    fileChange: function(e) {
    			var f = e.files[0]; //1
    			var FR = new FileReader();
    			var _this = this;

    			FR.onload = function(f) {

    				_this.compressImg(this.result, 300, function(data,originalWidth,originalHeight) {
    					console.log("callback");
                        if (_this.preShow) {
                            console.log("img pre show");
                            _this.imgDom.src=data;
                        }
                        console.log("begin send img");
                        var json = {};
                        data ="+"+ data;//.substring(22);
                        json.imageData = encodeURIComponent(data);
                        console.log("begin post");
                        Ajax.post(_this.postUrl,
                            json,
                            function(data) {console.log("ajax return");
                                if (_this.callback != null)
                                    _this.callback(data);
                            }
                        );
    				});
    			};
    			FR.readAsDataURL(f); //onload
    		},
    		compressImg: function(imgData, maxHeight, onCompress) {
            			var _this = this;
            			if (!imgData)
            				return;
            			onCompress = onCompress || function() {};
            			maxHeight = maxHeight || this.maxHeight; //200px
            			var canvas = document.createElement('canvas');
            			var img = new Image();
            			console.log("maxHeight:" + maxHeight);
            			img.onload = function() {
                            if (img.height > maxHeight) { //
                                img.width *= maxHeight / img.height;
                                img.height = maxHeight;
                            }
                            canvas.width = img.width;
                            canvas.height = img.height;
            				var ctx = canvas.getContext("2d");
            				ctx.clearRect(0, 0, canvas.width, canvas.height); // canvas
            				//canvans canvas.width = img.width; canvas.height = img.height;
            				console.log("width:" + img.width + "height:" + img.height);
            				ctx.drawImage(img, 0, 0, img.width, img.height); // canvas
            				console.log("begin compress img");
            				onCompress(canvas.toDataURL("image/png"),img.width,img.height); //canvascanvas
            			};
            			// srcimgcanvas
            			console.log("begin origin data load:");
            			img.src = imgData;

            		},
            		init: function(jso) {
            			this.imgDom =this. $refs.img;//parseDom("<img class=\" img-upload\" alt=\"\"></img>");
            			this.maxHeight = jso.maxHeight||633;
            			this.maxWidth = jso.maxWidth||300;
            			this.postUrl = jso.postUrl||(PATH+"/image/upload.json");
            			var that =this;
            			this.callback = jso.callback||function(result){
                            //that.src=result.data;
                               that.$emit('input', result.data); // input 
// that.$emit('inputChange', result.data);
that.value=result.data;
// that.img=result.data;
            			};
            			this.fileInput=this.$refs.input;//parseDom("<input type=\"file\" style=\"display:none\"/>");
                        this.fileInput.addEventListener("change",function(){
                            that.fileChange(this);
                        });
                        this.imgDom.addEventListener("click",function(){
                             var evt = new MouseEvent("click", { bubbles: false, cancelable: true, view: window });
                           that.fileInput.dispatchEvent(evt);
                        });
            		},
     handleInput(event) {
          var value = event.target.value;
          this.$emit('input', value); // input 
        },
      onchange: function() {
        console.log("onchange");
        this.$emit('onchange', this.textvalue);
      }

    },
    watch: {

    },

    events: {

    }
  };
</script>
<style>
.img-upload{
    max-width:100%;
}
</style>


