<template>


 <div  ref="wrap" class="slider">
 <ul id="sa">

 <li v-for="item in imglist "><a :href="item.url" target="_blank"><img :src="item.pic" alt="e"/></a></li>
 </ul>
 </div>
</template>
<script type="text/javascript">
import zwIcon from '../icon/zwIcon.vue';

export default {
        name: 'zwCrousel',
         components:{zwIcon},
        props:
            ["imglist","width","height"]//"tabs"
        ,
        data () {
            return {

            };
        },
        computed: {

        },
        /*  watch: {
          imglist(val, oldVal) {
          console.log("crousle is changed");
                console.log("inputVal = " + val + " , oldValue = " + oldVal)

              }
              },*/
        updated(){
              yxMobileSlider(this.$refs.wrap ,{width:this.width||340,height:this.height||220,during:3000});
         },
        mounted () {
            console.log(this.$refs.wrap );
          //  yxMobileSlider(this.$refs.wrap ,{width:this.width||340,height:this.height||220,during:3000});

        },
        methods: {


        },


        events: {

        }
};



   var yxMobileSlider = function(id,settings){
        var defaultSettings = {
            width: 640, //
            height: 320, //
            during: 5000, //
            speed:30 //
        }
        settings = extend(  defaultSettings, settings);//
        return  function(){
            var _this  = null;
            if(id instanceof HTMLElement){
                 _this = id;
            }else{
                 _this = document.getElementById(id)
             }
             var s = settings;//id
             append(_this,'<div class="focus"><div></div></div>');//
            var startX = 0, startY = 0; //
            var temPos; //
            var iCurr = 0; //
            var timer = null; //
            var oMover = _this.getElementsByTagName("ul" )[0]; //
            var oLi =_this.getElementsByTagName("li" );  //
            var imgs= _this.getElementsByTagName("img"); // 
            var num = oLi.length; //
            var oPosition = {}; //
            var moveWidth = s.width; //
            //
            width(_this,s.width);   //
            height(_this,s.height); //
           css(_this,{

                         position: 'relative',
                           overflow: 'hidden',
                        margin:'0 auto'
                      });

          //
            css(oMover,{
                position: 'absolute',
                left: 0
            });
            css(oLi,{
                float: 'left',
                display: 'inline'
            });

           css(imgs,{
                width: '100%',
                height: '100%'
            });
            //

            var oFocusContainer = document.getElementsByClassName("focus")[0];
            var  oFocusContainerDiv = oFocusContainer.getElementsByTagName("div")[0];
            for (var i = 0; i < num; i++) {
               append(oFocusContainerDiv,"<span></span>") ;

            }
            var oFocus = oFocusContainer.getElementsByTagName("span");
            if(oFocus.length==0){
                            return;
                        }
            css(oFocusContainer,{
                minHeight: oFocus.height * 2,
                position: 'absolute',
                bottom: 0,
                background: 'rgba(0,0,0,0.5)'
            })

           css(oFocus,{
                display: 'block',
                float: 'left',
                cursor: 'pointer'
            })
            width(oFocusContainerDiv,getStyleWidth(oFocus[0])*num*2);
           // console.log("oFocus.width"+);

           css(oFocusContainerDiv,{
                position: 'absolute',

                right: 10,
                top: '50%',
                marginTop: -oFocus.width / 2
            });

            addClass(oFocus[0],"current");
            //

            var resize=  function(){
                if (isMobile()) {
                    mobileSettings();
                    bindTochuEvent();
                }
                var thisWidth =getWidth(_this);
                var thisHeight = getHeight(_this);
                width(oLi,thisWidth);
                height(oLi,thisHeight);//

                width(oMover,num * thisWidth);
                width(oFocusContainer,thisWidth);//alert(getWidth(oFocusContainer));
                height(oFocusContainer,thisHeight * 0.15);
                css(oFocusContainer,{
                    zIndex: 2
                });//
                //_this.fadeIn(300);
                css(_this,{
                    display:"block"
                });
            };
            window.onload=resize();

            //BANNER
            if(s.autoMove){//
                           autoMove();
                      }
            //PC
            if (!isMobile()) {
                for(var i=0;i<oFocus.length;i++){
                    let oFocusIndex = i;
                     oFocus[i].addEventListener("mouseenter",
                   function(){

                        iCurr = oFocusIndex - 1;console.log(oFocusIndex);
                        stopMove();
                        doMove();

                    });
                    oFocus[i].addEventListener("onmouseleave",
                        function(){
                        autoMove();

                    });

                }

            }
            //
            function autoMove(){
                timer = setInterval(doMove, s.during);
            }
            //
            function stopMove(){
                clearInterval(timer);
            }
            //
            function doMove(){
                iCurr = iCurr >= num - 1 ? 0 : iCurr + 1;
                doAnimate(-moveWidth * iCurr);



                removeClass(oFocus,"current");

                addClass(oFocus[iCurr] ,"current");
            }
            //
            function bindTochuEvent(){
                oMover.addEventListener('touchstart', touchStartFunc, false);
                oMover.addEventListener('touchmove', touchMoveFunc, false);
                oMover.addEventListener('touchend', touchEndFunc, false);
            }
            //
            function touchPos(e){
                var touches = e.changedTouches, l = touches.length, touch, tagX, tagY;
                for (var i = 0; i < l; i++) {
                    touch = touches[i];
                    tagX = touch.clientX;
                    tagY = touch.clientY;
                }
                oPosition.x = tagX;
                oPosition.y = tagY;
                return oPosition;
            }
            //
            function touchStartFunc(e){
                clearInterval(timer);
                touchPos(e);
                startX = oPosition.x;
                startY = oPosition.y;
                temPos = getLeft(oMover);
            }
            //
            function touchMoveFunc(e){
                touchPos(e);
                var moveX = oPosition.x - startX;
                var moveY = oPosition.y - startY;
                if (Math.abs(moveY) < Math.abs(moveX)) {
                    e.preventDefault();
                    css(oMover,{
                        left: temPos + moveX
                    });
                }
            }
            //
            function touchEndFunc(e){
                touchPos(e);
                var moveX = oPosition.x - startX;
                var moveY = oPosition.y - startY;
                if (Math.abs(moveY) < Math.abs(moveX)) {
                    if (moveX > 0) {
                        iCurr--;
                        if (iCurr >= 0) {
                            var moveX = iCurr * moveWidth;
                            doAnimate(-moveX, autoMove);
                        }
                        else {alert(autoMove);
                            doAnimate(0, autoMove);
                            iCurr = 0;
                        }
                    }
                    else {
                        iCurr++;
                        if (iCurr < num && iCurr >= 0) {
                            var moveX = iCurr * moveWidth;
                            doAnimate(-moveX, autoMove);
                        }
                        else {
                            iCurr = num - 1;
                            doAnimate(-(num - 1) * moveWidth, autoMove);
                        }
                    }
                    removeClass(oFocus,"current");
                    addClass(oFocus[iCurr],"current");
                }
            }
            //
            function mobileSettings(){
                moveWidth = getWinWidth();
                var iScale =moveWidth / s.width;
                height(_this,s.height * iScale);
                width(_this,moveWidth);
                console.log("moveWidth:"+moveWidth);
                css(oMover,{
                    left: -iCurr * moveWidth
                });
            }
            //
            function doAnimate(iTarget, fn){

                animation(oMover, "left", iTarget, 200, fn);

            }
            //
            function isMobile(){
                if (navigator.userAgent.match(/Android/i) || navigator.userAgent.indexOf('iPhone') != -1 || navigator.userAgent.indexOf('iPod') != -1 || navigator.userAgent.indexOf('iPad') != -1) {

                    return true;
                }
                else {
                    return false;
                }
            }
        }("hello");
    }



</script>
<style>

</style>



